@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;600;700;1500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body{
    background-color: #F2E3E6;
    height: 100vh;
    width: 100vw;
}

#root
{
    height: 100vh;
    width: 100vw;
}

.title{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    color : #33201F;
    font-family: 'Poppins', sans-serif;
    font-size: 3.5em;
    font-weight: 550;
}

.inputfield{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color : #FFFFFF;
    color : #33201F;
    font-family: 'Poppins', sans-serif;
    border-radius: 15px;
    margin-bottom: 17px;
    padding: 3px;
    height: 5vh;
    width: 28vw;
    font-size: 1.5em
}

.login-form
{
    display: flex;
    flex-direction: column;
    width : 800px;
    height : 800px;
    background-color: #F9F1F3;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 1px 3px #b98892;
    border-radius: 15px;
}

.submit{
    background-color : #33201F;
    color : #FFFFFF;
    border : none;
    padding: 15px;
    padding-left: 50px;
    padding-right: 50px;
    font-family: 'Poppins', sans-serif;
    border-radius: 35px;
    margin-top: 30px;
    position: relative;
    align-items: center;
    font-size: 1.5em;
    font-weight: 1em;
}

.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
}

.centerer
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 25vh;
    width: 25vw;

}

.link
{
    position: fixed;
    bottom: 1%;
    right: 0.5%;
    font-family: 'Poppins', sans-serif;
    text-decoration: none;
    color : #FFFFFF;
    background-color: #ED5A5A;
    padding: 8px;
    border-radius: 17px;
}
